import React, { PureComponent } from 'react';
import { ThemeProvider, injectGlobal } from 'styled-components';

import 'sanitize.css';

import './global-styles';

import theme from './theme';
import Box from '../Box';
class Provider extends PureComponent {
  componentDidMount() {
    if (typeof window !== 'undefined') {
      const isIE = navigator.appName === 'Microsoft Internet Explorer' || Boolean(navigator.userAgent.match(/Trident/) || navigator.userAgent.match(/rv:11/));
      if (!isIE) {
        injectGlobal`
          svg {
            height: auto;
          }
        `;
      }
    }
  }

  render() {
    return (
      <ThemeProvider theme={theme}>
        <Box height="100%" fontSize={[14, null, 16]} {...this.props} />
      </ThemeProvider>
    )
  }
}

export default Provider
